.cont1{
    margin: 50px auto;
}

.card{
    margin: 10px auto;
    padding: 0px;
    box-shadow: 0px 5px 5px rgba(0,0,0,0.5);
    transition: 0.5s ease-in-out;
}

.card-img-top{
    height: 250px;
}

.card:hover{
    box-shadow: none;
    transform: scale(1.1);
    transition: 0.5s ease-in-out;
}

a{
    color: transparent;
    text-decoration: none !important;
}

.card-title{
    color: var(--blue) !important;
    text-align: center;
}